.weight-page-bar {
  position: fixed;
  z-index: 10;
  right: 0;
  left: 0;
  bottom: 0;
  display: table;
  width: 100%;
  height: 50px;
  padding: 0;
  table-layout: fixed;
  border-top: 0;
  border-bottom: 0;
}
.weight-page-bar a {
  display: table-cell;
  overflow: hidden;
  width: 1%;
  height: 50px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-decoration: none;
  color: #929292;
}
.weight-page-bar a i {
  font-weight: 400;
  font-style: normal;
  line-height: 1;
  display: inline-block;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  font-size: 24px;
  position: relative;
  z-index: 20;
  top: 3px;
  width: 24px;
  height: 24px;
  padding-top: 0;
  padding-bottom: 0;
}
.weight-page-bar a span {
  font-size: 11px;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}
.weight-page-bar a.active {
  color: #007aff;
}

.weight-page-header {
  position: fixed;
  z-index: 10;
  right: 0;
  left: 0;
  height: 44px;
  padding-right: 10px;
  padding-left: 10px;
  border-bottom: 0;
  background-color: #f7f7f7;
}
.weight-page-header .title {
  font-size: 17px;
  font-weight: 500;
  line-height: 44px;
  position: absolute;
  right: 40px;
  left: 40px;
  display: inline-block;
  overflow: hidden;
  width: auto;
  margin: 0;
  text-overflow: ellipsis;
}
.weight-row {
  display: flex;
}
.weight-row .col-1 {
  flex: 1;
}

.weight-shortcut {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 36px;
  font-size: 14px;
  line-height: 36px;
  border-bottom: 1px solid #ddd;
}
.weight-shortcut .key {
  color: #aaa;
  user-select: none;
}
.weight-shortcut kbd {
  padding: 0.1875rem 0.375rem;
  font-size: 0.875em;
  color: #fff;
  background-color: #212529;
  border-radius: 0.25rem;
}

.weight-search-bar {
  position: relative;
  display: block;
}
.weight-search-bar ul,
.weight-search-bar li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.weight-search-bar .search-input {
  position: relative;
}
.weight-search-bar .search-input .search-icon {
  position: absolute;
  left: 0.1875rem;
  top: 0.3125rem;
  width: 2.5rem;
  height: 2.5rem;
  overflow: hidden;
  border-radius: 1.5625rem;
  background-size: 100%;
  cursor: pointer;
}
.weight-search-bar .search-input input {
  width: 100%;
  height: 3.125rem;
  line-height: 3.125rem;
  font-size: 1rem;
  color: #999;
  border: none;
  outline: none;
  padding: 0 2.8125rem;
  border: 1px solid #e6e6e6;
}
.weight-search-bar .search-input .search-btn {
  position: absolute;
  left: 0;
  top: 0;
  width: 3.125rem;
  line-height: 3rem;
  background: transparent;
  border: none;
  font-size: 1.5625rem;
  color: #ddd;
  font-weight: bold;
  outline: none;
  padding: 0 0.625rem;
}
.weight-search-bar .search-input .clear-btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 3.125rem;
  line-height: 3rem;
  background: transparent;
  border: none;
  font-size: 1.5625rem;
  color: #ddd;
  font-weight: bold;
  outline: none;
  padding: 0 0.625rem;
}
.weight-search-bar .suggest-body,
.weight-search-bar .history-body {
  position: absolute;
  z-index: 100;
  width: 100%;
  border-radius: 0 0 0.625rem 0.625rem;
  background: #FFF;
  box-shadow: 0px 4px 5px 0px rgba(204, 204, 204, 0.5803921569);
  overflow: hidden;
}
.weight-search-bar .suggest-body li,
.weight-search-bar .history-body li {
  border-top: 1px solid #f2f2f2;
  line-height: 1.875rem;
  font-size: 0.875rem;
  padding: 0 1.5625rem;
  color: #777;
  cursor: pointer;
}
.weight-search-bar .suggest-body li .item-no,
.weight-search-bar .history-body li .item-no {
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  font-size: 0.75rem;
  line-height: 1.25rem;
  text-align: center;
  background: #e5e5e5;
  margin-right: 0.625rem;
  border-radius: 0.625rem;
  color: #999;
}
.weight-search-bar .suggest-body li.active,
.weight-search-bar .history-body li.active {
  background-color: rgba(153, 153, 153, 0.1);
}
.weight-search-bar .history-body li {
  display: flex;
}
.weight-search-bar .history-body li .item-text {
  flex: 1;
}
.weight-search-bar .history-body .clear-line {
  display: block;
  text-align: center;
}
.weight-search-bar .search-engine {
  position: absolute;
  top: 3.75rem;
  left: 0;
  width: 100%;
  background: #FFF;
  padding: 0.9375rem 0 0 0.9375rem;
  border-radius: 0.3125rem;
  box-shadow: 0px 0.3125rem 1.25rem 0px #d8d7d7;
  transition: all 0.3s;
  z-index: 999;
}
.weight-search-bar .search-engine .search-engine-header {
  padding-right: 0.625rem;
}
.weight-search-bar .search-engine .search-engine-header .search-engine-tool {
  float: right;
}
.weight-search-bar .search-engine .search-engine-body li {
  display: inline-block;
  width: 8.75rem;
  margin: 0 0.9375rem 0.9375rem 0;
  line-height: 1.875rem;
  font-size: 0.875rem;
  padding: 0.3125rem 0.625rem 0.3125rem 0.625rem;
  background: #f9f9f9;
  color: #999;
  cursor: pointer;
}
.weight-search-bar .search-engine .search-engine-body li span {
  width: 1.5625rem;
  height: 1.5625rem;
  border-radius: 0.9375rem;
  float: left;
  margin-right: 0.3125rem;
  margin-top: 0.1563rem;
  background-size: 100%;
}
.weight-search-bar .toggle-box {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  font-weight: 900;
  vertical-align: text-bottom;
  font-size: 1.25rem;
}
.weight-search-bar .toggle-box.checked::before {
  color: #5cb85c;
}

.weight-tab-bar {
  border: 0.0625rem solid #e1e1e1;
  display: flex;
  justify-content: space-around;
  flex-flow: row nowrap;
  z-index: 5;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3rem;
  background: #fff;
  border-top: 1px solid #ccc;
}
.weight-tab-bar a {
  width: 25%;
  text-align: center;
  display: block;
  float: left;
  padding-top: 0.25rem;
  box-sizing: border-box;
  position: relative;
  font-size: 0.9rem;
  text-decoration: none;
  position: relative;
  text-align: center;
}
.weight-tab-bar a .fa {
  font-size: 1.5rem;
  display: block;
}
.weight-tab-bar a.active {
  color: #4693d0;
}
.weight-tab-bar a.active .fa {
  color: #b4282d;
}